<!-- 【产品信息】弹窗页面 -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="product" ref="productForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="主键id" class="hidden" prop="id">
                <el-input type="hidden" v-model="product.id"/>
            </el-form-item>
            <el-form-item label="图片" prop="picture">
                <sui-photo type="product" v-model="product.picture" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="产品名称" prop="name">
                <el-input v-model="product.name" placeholder="请输入产品名称" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="产品类型" prop="productType">
                <el-select v-model="product.productType" placeholder="选择产品类型" :disabled="dialog.disabled" >
                    <el-option v-for="dict in productTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="产品价格" prop="price">
                <el-input v-model="product.price" placeholder="请输入产品价格" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="产品数量" prop="num">
                <el-input v-model="product.num" placeholder="请输入产品数量" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="产品描述" prop="remarks">
                <el-input type="textarea" v-model="product.remarks" placeholder="输入产品描述" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel">取 消</el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('productForm')">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
import suiPhoto from "@/components/sui-photo/sui-photo";
export default {
    components: {
        suiPhoto,
    },
    name: "productForm",
    data() {
      return {
        //弹窗设置
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //表单数据
        product: {
            id: "",
            picture: "",
            name: "",
            productType: "",
            price: "",
            num: "",
            remarks: ""
        },
        //表单校验
        rules: {
            picture: [{required: true, message: '请选择图片', trigger: 'change'}],
            name: [{required: true, message: '请输入产品名称', trigger: 'blur'}],
            productType: [{required: true, message: '请选择产品类型', trigger: 'change'}],
            price: [{required: true, message: '请输入产品价格', trigger: 'blur'}],
            num: [{required: true, message: '请输入产品数量', trigger: 'blur'}],
            remarks: [{required: true, message: '请输入产品描述', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //【产品类型】
        productTypeDict() {
            return this.$store.getters.getDictArray('product_type');
        },
    },
    methods: {
        //打开弹窗
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getProduct(option.id)
            }
        },
        //根据id获取数据
        getProduct(id) {
            this.$request.doGet("/api/product/get?id="+id).then(res => {
                if (res.isOk) {
                    this.product = res.obj;
                }
            });
        },
        //取消弹窗
        cancel: function () {
            this.dialog.open = false
        },
        //提交表单
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//表达校验
                    that.$request.doPost('/api/product/sub', that.product).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("提交成功");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //显示弹窗
        showDialog(title, disabled) {
            this.resetForm(this.$refs.productForm);//重置表单
            this.dialog.open = true;//打开弹窗
            this.dialog.title = title;//设置标题
            this.dialog.disabled = disabled;//是否可编辑
        }
    }
}
</script>

<style scoped>
</style>



