<!-- 【活动信息】弹窗页面 -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="activity" ref="activityForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="主键id" class="hidden" prop="id">
                <el-input type="hidden" v-model="activity.id"/>
            </el-form-item>
            <el-form-item label="活动标题" prop="title">
                <el-input v-model="activity.title" placeholder="请输入活动标题" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="开始日期" prop="beginDate">
                <el-date-picker v-model="activity.beginDate" placeholder="选择开始日期" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="结束日期" prop="endDate">
                <el-date-picker v-model="activity.endDate" placeholder="选择结束日期" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="活动内容" prop="content">
                <el-input type="textarea" v-model="activity.content" placeholder="输入活动内容" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel">取 消</el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('activityForm')">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
    components: {

    },
    name: "activityForm",
    data() {
      return {
        //弹窗设置
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //表单数据
        activity: {
            id: "",
            title: "",
            beginDate: "",
            endDate: "",
            activityStatus: "",
            content: ""
        },
        //表单校验
        rules: {
            title: [{required: true, message: '请输入活动标题', trigger: 'blur'}],
            beginDate: [{required: true, message: '请选择开始日期', trigger: 'change'}],
            endDate: [{required: true, message: '请选择结束日期', trigger: 'change'}],
            activityStatus: [{required: true, message: '请选择活动状态', trigger: 'change'}],
            content: [{required: true, message: '请输入活动内容', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //【活动状态】
        activityStatusDict() {
            return this.$store.getters.getDictArray('activity_status');
        },
    },
    methods: {
        //打开弹窗
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getActivity(option.id)
            }
        },
        //根据id获取数据
        getActivity(id) {
            this.$request.doGet("/api/activity/get?id="+id).then(res => {
                if (res.isOk) {
                    this.activity = res.obj;
                }
            });
        },
        //取消弹窗
        cancel: function () {
            this.dialog.open = false
        },
        //提交表单
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//表达校验
                    that.$request.doPost('/api/activity/sub', that.activity).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("提交成功");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //显示弹窗
        showDialog(title, disabled) {
            this.resetForm(this.$refs.activityForm);//重置表单
            this.dialog.open = true;//打开弹窗
            this.dialog.title = title;//设置标题
            this.dialog.disabled = disabled;//是否可编辑
        }
    }
}
</script>

<style scoped>
</style>



