<!-- 【客户信息】管理页面 -->
<template>
    <div class="app-container">
        <!-- 表单弹窗 -->
        <customer-form ref="customerDialog" @reloadList="getList"/>
        <!-- 查询条件 -->
        <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
            <el-form-item label="客户姓名" prop="name">
                <el-input v-model="queryForm.name" placeholder="请输入客户姓名" size="small"/>
            </el-form-item>
            <el-form-item label="客户级别" prop="level">
                <el-select v-model="queryForm.level" placeholder="请选择客户级别" size="small" clearable>
                    <el-option v-for="dict in levelDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="客户资产" prop="name">
              <el-input v-model="queryForm.assetsFrom" style="width: 80px" placeholder="从" size="small"/>
              <el-input v-model="queryForm.assetsTo" style="width: 80px" placeholder="到" size="small"/>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 查询按钮 -->
        <el-row :gutter="10" class="mb8">
            <el-col :span="1.5">
                <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增客户信息'})" v-hasPermi="['customer:add']">新增</el-button>
            </el-col>
            <el-col :span="1.5">
                <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['customer:delete']">批量删除</el-button>
            </el-col>
            <right-toolbar @queryTable="getList"></right-toolbar>
        </el-row>
        <!-- 表格区域 -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
            <el-table-column type="selection" align="center" width="50"/>
            <el-table-column label="id"  prop="id" v-if="false"/>
            <el-table-column label="序号" type="index" width="50"/>
            <el-table-column label="客户姓名" prop="name" sortable/>
            <el-table-column label="证件号码" prop="cardNo" sortable/>
            <el-table-column label="客户级别" prop="level" sortable>
                <template slot-scope="scope">
                    <span v-for="dict in levelDict" v-if="dict.dictValue==scope.row.level">{{dict.dictLabel}}</span>
                </template>
            </el-table-column>
            <el-table-column label="联系方式" prop="phone" sortable/>
            <el-table-column label="客户资产" prop="assets" sortable/>
            <el-table-column label="客户标签" prop="label" show-overflow-tooltip sortable/>
            <el-table-column label="操作" align="center"  width="300" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看客户信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['customer:view']">查看</el-button>
                    <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改客户信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['customer:edit']">修改</el-button>
                    <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['customer:delete']">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :background="true"
                :current-page="queryForm.current"
                :page-sizes="[5,10,20,30,50]"
                :page-size="queryForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import customerForm from './customerForm'
    export default {
        components: {customerForm},
        data() {
            return {
              loading: true,//是否加载完成
              selectedData: [],//勾选数据
              total: 0, //记录数
              dataList: [],
              queryForm: {
                current: 1,
                size: 10,
                id: "",
                name: "",
                officeId: "",
                cardNo: "",
                level: "",
                phone: "",
                assets: "",
                assetsFrom: "",
                assetsTo: "",
                label: ""
            }
          };
        },
        mounted() {
            //初始化数据
            this.getList();
        },
        computed: {
            //【客户级别】
            levelDict() {
                return this.$store.getters.getDictArray('customer_level');
            },
        },
        methods: {
            //点击搜索
            handleQuery() {
                this.queryForm.current = 1;
                this.getList();
            },
            //查询列表数据
            getList() {
                this.$request.doPost('/api/customer/getPage', this.queryForm).then(res => {
                    if (res.isOk) {
                        this.loading = false;
                        this.dataList = res.obj.records;
                        this.total = res.obj.total;
                    }
                })
            },
            //重置查询
            resetQuery() {
                this.$refs['queryForm'].resetFields();
            },
            //打开弹窗
            openDialog(option) {
                this.$refs.customerDialog.open(option);
            },
            //选择页数
            handleSizeChange: function (size) {
                this.queryForm.size = size;
                this.getList();
            },
            //选择页码
            handleCurrentChange: function (currentPage) {
                this.queryForm.current = currentPage;
                this.getList();
            },
            //获取勾选数据
            handleSelectionChange(data) {
                this.selectedData = data;
            },
            //勾选批量删除
            handleDeleteAll() {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    if (that.selectedData.length > 0) {
                        that.selectedData.forEach(function (item, index) {
                            that.delete(item.id);
                        });
                        that.showSuccess("删除成功")
                    } else {
                        that.showWarn("至少选择一条数据！")
                    }
                });
            },
            //选中行删除
            handleDelete(id) {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    that.delete(id);
                    that.showSuccess("删除成功");
                });
            },
            //删除方法
            delete(id) {
                this.$request.doGet('/api/customer/delete?id=' + id).then(res => {
                    if (res.isOk) {
                        this.getList();
                    };
                });
            },
        }
    };
</script>



