<!-- 【活动信息】管理页面 -->
<template>
    <div class="app-container">
        <!-- 表单弹窗 -->
        <activity-form ref="activityDialog" @reloadList="getList"/>
        <!-- 查询条件 -->
        <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
            <el-form-item label="活动标题" prop="title">
                <el-input v-model="queryForm.title" placeholder="请输入活动标题" size="small"/>
            </el-form-item>
            <el-form-item label="开始日期" prop="beginDate">
                <el-date-picker v-model="queryForm.beginDate" placeholder="请选择开始日期" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
            </el-form-item>
            <el-form-item label="结束日期" prop="endDate">
                <el-date-picker v-model="queryForm.endDate" placeholder="请选择结束日期" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
            </el-form-item>
            <el-form-item label="活动状态" prop="activityStatus">
                <el-select v-model="queryForm.activityStatus" placeholder="请选择活动状态" size="small" clearable>
                    <el-option v-for="dict in activityStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 查询按钮 -->
        <el-row :gutter="10" class="mb8">
            <el-col :span="1.5">
                <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增活动信息'})" v-hasPermi="['activity:add']">新增</el-button>
            </el-col>
            <el-col :span="1.5">
                <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['activity:delete']">批量删除</el-button>
            </el-col>
            <right-toolbar @queryTable="getList"></right-toolbar>
        </el-row>
        <!-- 表格区域 -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
            <el-table-column type="selection" align="center" width="50"/>
            <el-table-column label="id"  prop="id" v-if="false"/>
            <el-table-column label="序号" type="index" width="50"/>
            <el-table-column label="活动标题" prop="title" sortable/>
            <el-table-column label="活动状态" prop="activityStatus" sortable>
                <template slot-scope="scope">
                    <span v-for="dict in activityStatusDict" v-if="dict.dictValue==scope.row.activityStatus">{{dict.dictLabel}}</span>
                </template>
            </el-table-column>
            <el-table-column label="活动内容" prop="content" show-overflow-tooltip sortable/>
            <el-table-column label="操作" align="center"  width="380" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看活动信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['activity:view']">查看</el-button>
                    <el-button size="mini" type="primary" :disabled="scope.row.activityStatus==2" plain icon="el-icon-edit" @click="openDialog({title:'修改活动信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['activity:edit']">修改</el-button>
                    <el-button size="mini" type="primary" :disabled="scope.row.activityStatus==2" plain icon="el-icon-edit" @click="subActivity(scope.row)" v-hasPermi="['activity:edit']">提交</el-button>
                    <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['activity:delete']">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :background="true"
                :current-page="queryForm.current"
                :page-sizes="[5,10,20,30,50]"
                :page-size="queryForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import activityForm from './activityForm'
    export default {
        components: {activityForm},
        data() {
            return {
              loading: true,//是否加载完成
              selectedData: [],//勾选数据
              total: 0, //记录数
              dataList: [],
              queryForm: {
                current: 1,
                size: 10,
                id: "",
                title: "",
                beginDate: "",
                endDate: "",
                activityStatus: "",
                content: ""
            }
          };
        },
        mounted() {
            //初始化数据
            this.getList();
        },
        computed: {
            //【活动状态】
            activityStatusDict() {
                return this.$store.getters.getDictArray('activity_status');
            },
        },
        methods: {
          subActivity(row){
            const that = this;
            that.showConfirm("确认提交吗?", function () {
               row.activityStatus = 2;
               that.$request.doPost('/api/activity/sub', row).then(res => {
                  if (res.isOk) {
                    that.showSuccess("提交成功");
                    that.getList();
                 };
              });
            })
          },
            //点击搜索
            handleQuery() {
                this.queryForm.current = 1;
                this.getList();
            },
            //查询列表数据
            getList() {
                this.$request.doPost('/api/activity/getPage', this.queryForm).then(res => {
                    if (res.isOk) {
                        this.loading = false;
                        this.dataList = res.obj.records;
                        this.total = res.obj.total;
                    }
                })
            },
            //重置查询
            resetQuery() {
                this.$refs['queryForm'].resetFields();
            },
            //打开弹窗
            openDialog(option) {
                this.$refs.activityDialog.open(option);
            },
            //选择页数
            handleSizeChange: function (size) {
                this.queryForm.size = size;
                this.getList();
            },
            //选择页码
            handleCurrentChange: function (currentPage) {
                this.queryForm.current = currentPage;
                this.getList();
            },
            //获取勾选数据
            handleSelectionChange(data) {
                this.selectedData = data;
            },
            //勾选批量删除
            handleDeleteAll() {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    if (that.selectedData.length > 0) {
                        that.selectedData.forEach(function (item, index) {
                            that.delete(item.id);
                        });
                        that.showSuccess("删除成功")
                    } else {
                        that.showWarn("至少选择一条数据！")
                    }
                });
            },
            //选中行删除
            handleDelete(id) {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    that.delete(id);
                    that.showSuccess("删除成功");
                });
            },
            //删除方法
            delete(id) {
                this.$request.doGet('/api/activity/delete?id=' + id).then(res => {
                    if (res.isOk) {
                        this.getList();
                    };
                });
            },
        }
    };
</script>



