<!-- 【客户信息】弹窗页面 -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="customer" ref="customerForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="主键id" class="hidden" prop="id">
                <el-input type="hidden" v-model="customer.id"/>
            </el-form-item>
            <el-form-item label="客户姓名" prop="name">
                <el-input v-model="customer.name" placeholder="请输入客户姓名" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="所属机构" prop="officeId">
                <sui-office v-model="customer.officeId"  placeholder="请选择所属机构" :disabled="true"/>
            </el-form-item>
            <el-form-item label="证件号码" prop="cardNo">
                <el-input v-model="customer.cardNo" placeholder="请输入证件号码" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="客户级别" prop="level">
                <el-select v-model="customer.level" placeholder="请选择客户级别" :disabled="dialog.disabled" >
                    <el-option v-for="dict in levelDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="联系方式" prop="phone">
                <el-input v-model="customer.phone" placeholder="请输入联系方式" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="客户资产" prop="assets">
                <el-input v-model="customer.assets" placeholder="请输入客户资产" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="客户标签" prop="label">
                <el-input type="textarea" v-model="customer.label" placeholder="请输入客户标签" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel">取 消</el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('customerForm')">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
    components: {

    },
    name: "customerForm",
    data() {
      return {
        //弹窗设置
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //表单数据
        customer: {
            id: "",
            name: "",
            officeId: this.$store.getters.userInfo.officeId,
            cardNo: "",
            level: "",
            phone: "",
            assets: "",
            label: ""
        },
        //表单校验
        rules: {
            name: [{required: true, message: '请输入客户姓名', trigger: 'blur'}],
            officeId: [{required: true, message: '请选择所属机构', trigger: 'change'}],
            cardNo: [{required: true, message: '请输入证件号码', trigger: 'blur'}],
            level: [{required: true, message: '请选择客户级别', trigger: 'change'}],
            phone: [{required: true, message: '请输入联系方式', trigger: 'blur'}],
            assets: [{required: true, message: '请输入客户资产', trigger: 'blur'}],
            label: [{required: true, message: '请输入客户标签', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //【客户级别】
        levelDict() {
            return this.$store.getters.getDictArray('customer_level');
        },
    },
    methods: {
        //打开弹窗
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getCustomer(option.id)
            }
        },
        //根据id获取数据
        getCustomer(id) {
            this.$request.doGet("/api/customer/get?id="+id).then(res => {
                if (res.isOk) {
                    this.customer = res.obj;
                }
            });
        },
        //取消弹窗
        cancel: function () {
            this.dialog.open = false
        },
        //提交表单
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//表达校验
                    that.$request.doPost('/api/customer/sub', that.customer).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("提交成功");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //显示弹窗
        showDialog(title, disabled) {
            this.resetForm(this.$refs.customerForm);//重置表单
            this.dialog.open = true;//打开弹窗
            this.dialog.title = title;//设置标题
            this.dialog.disabled = disabled;//是否可编辑
        }
    }
}
</script>

<style scoped>
</style>



